/* FONT-FACE */

@font-face {
	font-family: "RijksOverheidSans";
	src: url("../fonts/rijksoverheidsanstext-regular_3_0-webfont.eot");
	src: url("../fonts/rijksoverheidsanstext-regular_3_0-webfont.eot?#iefix") format("embedded-opentype"),
			url("../fonts/rijksoverheidsanstext-regular_3_0-webfont.woff2") format("woff2"),
			url("../fonts/rijksoverheidsanstext-regular_3_0-webfont.woff") format("woff"),
			url("../fonts/rijksoverheidsanstext-regular_3_0-webfont.ttf") format("truetype"),
			url("../fonts/rijksoverheidsanstext-regular_3_0-webfont.svg#RijksOverheidSansRegular") format("svg");
	font-weight: normal;
	font-style: normal;
}

@font-face {
	font-family: "RijksOverheidSans";
	src: url("../fonts/rijksoverheidsanstext-italic_3_0-webfont.eot");
	src: url("../fonts/rijksoverheidsanstext-italic_3_0-webfont.eot?#iefix") format("embedded-opentype"),
			url("../fonts/rijksoverheidsanstext-italic_3_0-webfont.woff2") format("woff2"),
			url("../fonts/rijksoverheidsanstext-italic_3_0-webfont.woff") format("woff"),
			url("../fonts/rijksoverheidsanstext-italic_3_0-webfont.ttf") format("truetype"),
			url("../fonts/rijksoverheidsanstext-italic_3_0-webfont.svg#RijksOverheidSansRegular") format("svg");
	font-weight: normal;
	font-style: italic;
}

@font-face {
	font-family: "RijksOverheidSans";
	src: url("../fonts/rijksoverheidsansheading-bold_3_0-webfont.eot");
	src: url("../fonts/rijksoverheidsansheading-bold_3_0-webfont.eot?#iefix") format("embedded-opentype"),
			url("../fonts/rijksoverheidsansheading-bold_3_0-webfont.woff2") format("woff2"),
			url("../fonts/rijksoverheidsansheading-bold_3_0-webfont.woff") format("woff"),
			url("../fonts/rijksoverheidsansheading-bold_3_0-webfont.ttf") format("truetype"),
			url("../fonts/rijksoverheidsansheading-bold_3_0-webfont.svg#RijksOverheidSansBold") format("svg");
	font-weight: bold;
	font-style: normal;
}

@font-face {
	font-family: "RijksOverheidSans";
	src: url("../fonts/rijksoverheidsanstext-bolditalic_3_0-webfont.eot");
	src: url("../fonts/rijksoverheidsanstext-bolditalic_3_0-webfont.eot?#iefix") format("embedded-opentype"),
			url("../fonts/rijksoverheidsanstext-bolditalic_3_0-webfont.woff2") format("woff2"),
			url("../fonts/rijksoverheidsanstext-bolditalic_3_0-webfont.woff") format("woff"),
			url("../fonts/rijksoverheidsanstext-bolditalic_3_0-webfont.ttf") format("truetype"),
			url("../fonts/rijksoverheidsanstext-bolditalic_3_0-webfont.svg#RijksOverheidSansBold") format("svg");
	font-weight: bold;
	font-style: italic;
}